import { Value } from '@ali/4ever-cangjie';

export default function isSelectWholeBlocks(value: Value) {
  const { selection, startBlock, endBlock, document } = value;
  const { start, end } = selection.convertToTextPoints(document);
  if (!startBlock || !endBlock) return false;
  return start.isAtStartOfNode(startBlock) && end.isAtEndOfNode(endBlock);
}
